.todo{
    width: 600px;
    min-height: 732px;
    border-radius: 20px;
    background: #eae3e8d4;
    margin:auto;
    margin-top: 174px;
    display: flex;
    flex-direction: column;
    padding: 0px 44px;
    padding-bottom: 30px;
    margin-bottom: 100px;
}
.todo-header{
    margin-top: 84px;
    color:#105fdf;
    font-size: 36px;
    font-weight: 600;
    line-height: 34px;
}
.todo-add{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45px;
}
.todo-input{
    border-radius: 50px;
    background: #edeef0;
    border: none;
    outline: none;
    width: 576px;
    height: 80px;
    padding-left: 35px;
    font-size: 20px;
}
.todo-add-btn{
    border-radius: 50px;
    background: #394dffde;
    width: 187px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items:center;
    margin-left: -100px;
    color: #eadaea;
    font-size: 26px;
    font-weight: 600;
    cursor: pointer;

}
.love{
    display: flex;
    padding-top: 380px;
    padding-left: 220px;
    font-size: 30px;
    font-weight: 600;
    line-height: 34px;
    color:#105fdf;

}
.sarvesh{
    display: flex;
    padding-top: 10px;
    padding-left: 190px;
    font-size: 30px;
    font-weight: 500;
    line-height: 34px;
    color:#105fdf;

}